import React, { useContext } from 'react'
import { AuthContext } from '../context/AuthContext';
import { useNavigate,Link } from 'react-router-dom';
import bbva from '../assets/img/logo2.png'


export default function Navbar() {
    let navigate = useNavigate();
    
    const { isAuthenticated, setIsAuthenticated, user, setUser, onRoom } = useContext(AuthContext);

    //eslint-disable-next-line
    const logOut = e => {
        e.preventDefault();
        setUser(null);
        setIsAuthenticated(false);
        localStorage.removeItem('username');
        navigate('/');
    };


    return (
        <nav className={isAuthenticated ? "navbar navbar-expand-lg navbar-dark bg-primary " : "navbar navbar-expand-lg navbar-dark bg-success "} >
            {isAuthenticated ?   <div className="ms-5 me-5 container-fluid">
    <img src={bbva}  className="img-fluid d-inline-block align-top" width="70" height="50"  alt="logo de BBVA"/>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarColor01" aria-controls="navbarColor01" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarColor01">

    <ul className="navbar-nav me-auto">
      </ul>
      {onRoom !== "" ? <h4 className='text-light'>{onRoom}</h4> : null}
      <ul className="navbar-nav me-auto">
      </ul>

        
     
    
      <li className="nav-link dropdown me-5 ">
          <a className="dropdown-toggle text-light a" href='/#' data-bs-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">{user}</a>
          <div className="dropdown-menu">
            {onRoom !== "" ? <Link className="dropdown-item" to="/"><h6>Volver al inicio</h6></Link> : null}
            <h6 className="dropdown-item"  onClick={logOut}>Cerrar sesion</h6>

          </div>
        </li>
       
    </div>
  </div>:<h2 className="navbar-brand mx-auto">Iniciar sesion</h2>}

        </nav>
    )
}