import React, { useEffect, useContext, useState } from 'react';
import { AuthContext } from '../context/AuthContext';
import { useParams, useNavigate } from 'react-router-dom';
import io from 'socket.io-client';
//eslint-disable-next-line
let socket;
export default function Fibonacci() {
  const navigate = useNavigate();
  // eslint-disable-next-line
  const { user, setOnRoom } = useContext(AuthContext);
  const username = localStorage.getItem('username');
  const [users, setUsers] = useState([]);
    //eslint-disable-next-line
  const [userProperties, setUserProperties] = useState([]);
  const [viewers, setViewers] = useState([]);
  const [average, setAverage] = useState(0);
  const [spectate, setSpectate] = useState(false);
  const [voted, setVoted] = useState(false);
  const [votes, setVotes] = useState([]);
  const params = useParams();
  const { room } = params;

  useEffect(() => {
    let viewer = localStorage.getItem('viewer');
    if (viewer == null || viewer === "false") {
      localStorage.setItem('viewer', false);
      viewer = false;
    } else {
      setSpectate(true)
      viewer = true;
    }
    if (user) {
      setOnRoom(room)

      //socket with reconexxion 
      socket = io({ reconnection: true, reconnectionDelay: 1000, reconnectionDelayMax: 10000, reconnectionAttempts: Infinity });

      socket.emit('join', { user: user, username: username, room: room, viewer: viewer });

      socket.on('joined', (obj) => {
        
        if (users.length === 0) {
          let usuarios = obj.users;
          let espectadores = obj.viewers;
          if (viewer === false) {
            usuarios.forEach(user => {
              if (user.name === username) {
                setUserProperties(user)
              }
            });
          } else {
            espectadores.forEach(user => {
              if (user.name === username) {
                setUserProperties(user)
              }
            });
          }
        }
        setUsers(obj.users);
        setViewers(obj.viewers);

      });

      socket.on('selected', (obj) => {
        setUsers(obj.users);
      });

      socket.on('revealed', (obj) => {
        let count = 0;
        let total = 0;
        let totalVotos = []
        for (let user of obj.users) {
          if (user.voted) {
            count++;
            total = total + user.number;
            totalVotos.push(user)
          }
        }
        totalVotos.sort((a, b) => a.number - b.number);
        setVotes(totalVotos);
        if (count > 0) {
          let resultado = total / count;

          if (resultado !== Math.floor(resultado)) {
            resultado = resultado.toFixed(2);
          }
          setAverage(resultado);
          setVoted(true);
        } else {
          setAverage(0)
          setVoted(true);
        }
      });

      socket.on('resetVote', (obj) => {
        setUsers(obj.users);
        setVotes([]);
        setAverage(0);

        setVoted(false);

      });


      socket.on('newViewer', (obj) => {
        setViewers(obj.viewers);
        setUsers(obj.users);
      });

      socket.on('disconnected', (obj) => {
        setUsers(obj.users);
        setViewers(obj.viewers);
      });
      socket.on('newAdmin', (obj) => { 
        let usuarios = obj.users;
        let espectadores = obj.viewers;
        if (viewer === false) {
          usuarios.forEach(user => {
            if (user.name === username) {
              setUserProperties(user)
            }
          });
        } else {
          espectadores.forEach(user => {
            if (user.name === username) {
              setUserProperties(user)
            }
          });
        }
       
      })

    } else {

      let nickname = prompt("OH NO! Parece que no has iniciado sesion, por favor ingrese su nombre");
      if (nickname === null || nickname === "") {
        localStorage.setItem('room', room);
        navigate('/');
      } else {
        localStorage.setItem('username', nickname);
        window.location.reload();
      }

    }

    return () => {
      if (user) {
        socket.disconnect();
      }
      setOnRoom('');
    };

    //eslint-disable-next-line
  }, [])
  const SelectOption = (number) => {

    socket.emit('select', { number: number, name: user, room: room });

  }

  const Revelar = () => {
    socket.emit('reveal', (room));
  }
  const NuevaVotacion = () => {
    socket.emit('newVote', room);
  }

  const spectateMode = () => {
    if (spectate) {
      setSpectate(false);
      socket.emit('spectate', { name: user, room: room, spectate: spectate });
      localStorage.setItem('viewer', false);
    } else {
      setSpectate(true);
      socket.emit('spectate', { name: user, room: room, spectate: spectate });
      localStorage.setItem('viewer', true);
    }
  }


  return (
    <div className="m-3">
      {/* div de modo espectador */}
      <div className="d-flex flex-row-reverse ">
        <div className="form-check form-switch px-4">
          <label className="form-check-label checkbox-text "><h4 className="form-text2">Modo Espectador</h4></label>
          <input className="form-check-input checkbox-big" type="checkbox" onChange={spectateMode} checked={spectate} />
        </div>
      </div>

      <div className="d-flex justify-content-around">

        {/* div de lista de integrantes */}
        <div className="text-center ">
          <div className="card border-primary">
            <div className="card-header ">
              <h1 className=" mt-1"> Integrantes</h1>
            </div>
            <div className="card-body">
              {viewers.map((user, key) => { return (<h5 key={key}>{user.name} <i className="bi bi-eye-fill text-primary"></i></h5>) })}
              {users.map((user, key) => { return (<h5 key={key}>{user.name} <i className="bi bi-person-fill text-success"></i></h5>) })}
            </div>

          </div>

        </div>

        {/* div de cartas y boton para revelarlas */}
        <div className="container">

      {/* {userProperties.role === "admin" ?  */}
         <div>          {voted === true ?
            <div className='mx-auto text-center'>
              <button className="mx-auto btn btn-primary col-md-4 " onClick={NuevaVotacion}>
                <h4 className='text-center'>Nueva votacion</h4></button> </div> :
            <div className='mx-auto text-center'> <button className="mx-auto btn btn-outline-primary col-md-4 " onClick={Revelar}>
              <h4 className='text-center'>Revelar cartas</h4></button> </div>
          }</div>
          {/* : null} */}

          <div className="container mt-4">

            {voted === true ?

              <div>
                <div className='container'>

                  <div className="row row-cols-6">
                    {votes.map((integrante, key) => <div className="col" key={key}><div className="poker-card-voted"><h4 className='text-center'>{integrante.number}</h4></div><h6 className='mx-auto'>{integrante.name}</h6> </div>)}
                  </div>
                </div>
                <h1>

                  Promedio: {average}
                </h1>
              </div>
              :
              <div className="container">

                <div className="row row-cols-6">
                  {users.map((integrante, key) => <div className="col" key={key}><div className={(integrante.voted) ? "poker-card" : "poker-card-hidden"}></div><h6 className='mx-auto'>{integrante.name}</h6> </div>)}
                </div>
              </div>

            }



          </div>

        </div>




      </div>
      {
        voted === false ?

          <div className="d-flex justify-content-center mt-5" id="cartas">
            {spectate === false ? <ul>
              <li className="card-li" >
                <button className="card-button" onClick={() => { SelectOption(0) }} >
                  <span className="card-font">0</span>
                </button>
              </li>
              <li className="card-li" >
                <button className="card-button" onClick={() => { SelectOption(1) }} >
                  <span className="card-font">1</span>
                </button>
              </li>
              <li className="card-li" >
                <button className="card-button" onClick={() => { SelectOption(2) }}>
                  <span className="card-font">2</span>
                </button>
              </li>
              <li className="card-li" >
                <button className="card-button" onClick={() => { SelectOption(3) }}>
                  <span className="card-font">3</span>
                </button>
              </li>
              <li className="card-li" >
                <button className="card-button" onClick={() => { SelectOption(5) }}>
                  <span className="card-font">5</span>
                </button>
              </li>
              <li className="card-li mb-3" >
                <button className="card-button" onClick={() => { SelectOption(8) }} >
                  <span className="card-font">8</span>
                </button>
              </li>
              <li className="card-li" >
                <button className="card-button" onClick={() => { SelectOption(13) }} >
                  <span className="card-font">13</span>
                </button>
              </li>
              <li className="card-li" >
                <button className="card-button" onClick={() => { SelectOption(21) }} >
                  <span className="card-font">21</span>
                </button>
              </li>
              <li className="card-li" >
                <button className="card-button" onClick={() => { SelectOption(34) }} >
                  <span className="card-font">34</span>
                </button>
              </li>
              <li className="card-li" >
                <button className="card-button" onClick={() => { SelectOption(55) }} >
                  <span className="card-font">55</span>
                </button>
              </li>
              <li className="card-li" >
                <button className="card-button" onClick={() => { SelectOption(89) }} >
                  <span className="card-font">89</span>
                </button>
              </li>
            </ul> : null}
          </div>
          : null}
    </div>
  )
}
