//eslint-disable-next-line
import React, { useContext, useState,useEffect } from 'react';
import { AuthContext } from '../context/AuthContext';
import { useNavigate } from 'react-router-dom';
import bancoLogo from '../assets/img/banco.png';

export default function Home() {
    let navigate = useNavigate();
    const { isAuthenticated, setIsAuthenticated, setUser } = useContext(AuthContext);
    const [username, setUsername] = useState('');
    const [room, setRoom] = useState('');
    const roomHistory = localStorage.getItem('room');
    const [styleRoom,setStyleRoom] = useState('fibonacci');    


    const onSubmit = e => {

        e.preventDefault();
        localStorage.setItem('username', username);
        setUser(username);
        setIsAuthenticated(true);
    }

    const joinRoom = e => {
        e.preventDefault();
        if (roomHistory != null) {
            setRoom(roomHistory);
            localStorage.removeItem('room');
            navigate(`/${styleRoom}/${roomHistory}`);
        } else {
           navigate(`/${styleRoom}/${room}`);
        }

    }
    const onChange =(roomID)=>{
     setStyleRoom(roomID);
    }

    return (
        <div>

            {roomHistory ?
                <div className="col-md-3 ms-auto pe-3 text-center">
                    <div className="card m-3 border-primary">


                        <form onSubmit={joinRoom} className="m-2">


                            <div className="form-group ">

                                <h3>Entrar a la sala: <span className='text-primary'>{roomHistory}</span></h3>
                                <button className="btn col-4 btn-primary btn-block" type="submit">Entrar</button>
                            </div>
                        </form>


                    </div>
                </div>
                : null}

            {isAuthenticated ?
                <div className="mt-1 p-3">
                    <div className="col-md-6 mx-auto text-center">
                        <div className="card  border-primary">
                            <div className="card-header ">
                                <h1 className=" mt-1">Crear sala </h1>
                            </div>
                            <div className="card-body">
                                <img src={bancoLogo} className="card-img-top img-fluid rounded" style={{ maxWidth: '50%', maxHeight: '50%' }} alt="..." />
                                <form onSubmit={joinRoom}>
                                    <div className="form-group mt-3">
                                        <label htmlFor="username">Nombre de la sala</label>
                                        <input type="text" name="room" onChange={e => setRoom(e.target.value)} value={room} className="form-control text-center mb-3" placeholder="Ingresá el nombre de la sala" />
                                    </div>
                                    <div className="form-group mt-3 mb-3">
                                        <label htmlFor="styleRoom">Tipo de votacion</label>
                                        <select className="form-select" aria-label="Default select example"   onChange={(e)=>onChange(e.target.value)}>
                                            <option value="fibonacci" >fibonacci (0,1,2,3,5,8,7,8,13,21,23,34,55,89)</option>
                                            <option value="fibonacci_modified">fibonacci Modificado (1/2,1,2,3,5,8,13,20,40,100,?,∞,coffe)</option>
                                            
                                        </select>
                                    </div>
                                    <div className="form-group">
                                        <button className="btn col-12 btn-primary btn-block" type="submit">Iniciar</button>
                                    </div>
                                </form>
                            </div>

                        </div>
                    </div>
                </div>

                :
                <div className="row mt-5 p-3">
                    <div className="col-md-6 mx-auto text-center">
                        <div className="card  border-success">
                            <div className="card-header ">
                                <h1 className=" mt-1"> INICIAR SESION</h1>
                            </div>
                            <div className="card-body">
                                <form onSubmit={onSubmit}>
                                    <img src={bancoLogo} className="card-img-top img-fluid rounded" style={{ maxWidth: '50%', maxHeight: '50%' }} alt="..." />
                                    <div className="form-group mt-3">
                                        <label htmlFor="username">Nombre del usuario</label>
                                        <input type="text" name="username" onChange={e => setUsername(e.target.value)} value={username} className="form-control text-center mb-3" placeholder="Ingresá tu nombre de usuario" />
                                    </div>
                                    <div className="form-group">
                                        <button className="btn col-12 btn-success text-light btn-block" type="submit">Iniciar Sesion</button>
                                    </div>
                                </form>
                            </div>

                        </div>
                    </div>
                </div>


            }



        </div>
    )
}
